@import '../sass/mixins/_mixins.sass';

.section-welcome {
  padding: 7.2rem 0;
  position: relative;
  z-index: 1;
  background-image: linear-gradient(to bottom, $socket-d, $surface-d-bg-d);
  overflow: hidden;
  @include lg {
    // display: none;
    padding: 3.2rem 0 2.4rem;
    background-image: linear-gradient(to bottom, $socket-d, $surface-d-bg-d);
  }
  .row-mb-144 {
    margin-bottom: 14.4rem;
    @include lg {
      margin-bottom: 4.8rem;
    }
  }
  .container {
    position: relative;
  }
  .row {
    position: relative;
    z-index: 10;
  }
}
// @include 
.welcome {
  &-line {
    position: absolute;
    top: 44.4rem;
    left: calc(50% - 40.8rem);
    z-index: 1;
    @include xl {
      // top: 41rem;
    }
    @include media-i(991, 1399) {
      top: 42rem;
    }
    img {
      transform: translateX(10rem);
      @include media-i(991, 1399) {
        transform: translateX(2rem);
        max-width: 90%;
      }
    }
  }
  &__img {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    @include lg {
      margin-bottom: 1.6rem;
      justify-content: center;
    }
    &--right {
      justify-content: flex-end;
      @include lg {
        justify-content: center;
      }
    }
  }
}
