@import '../sass/mixins/_mixins.sass';

.flip-clock {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  & > span {
    background-color: $white;
    border-radius: .2rem;
    width: 5.6rem;
    height: 7.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    &:first-child {
      border-radius: .4rem .2rem .2rem .4rem;
    }
    &:last-child {
      border-radius: .2rem .4rem .4rem .2rem;
    }
    &:not(:last-child) {
      margin-right: .4rem;
    }
  }
  &__card {
    font-family: Karantina;
    font-size: 4.8rem;
    font-weight: normal;
    line-height: 0.75em;
    text-align: center;
    color: $text-l-text-l-80;
    font-weight: 600;
  }
  &__slot {
    @include font(1.3rem, 1.23em, 600, 1px);
    text-align: center;
    color: $text-l-60;
    margin-top: .8rem;
  }
}

.flip-card {
  display: block;
  position: relative;
}

.flip-card__top,
.flip-card__bottom,
.flip-card__back-bottom,
.flip-card__back::before,
.flip-card__back::after {
  display: block;
}

.flip-card__top-4digits,
.flip-card__bottom-4digits,
.flip-card__back-bottom-4digits,
.flip-card__back-4digits::before,
.flip-card__back-4digits::after {
  display: block;
}

.flip-card__bottom,
.flip-card__back-bottom,
.flip-card__bottom-4digits,
.flip-card__back-bottom-4digits {
  color: #ffdc00;
  position: absolute;
  top: 50%;
  left: 0;
  border-top: solid 1px #000;
  background: #393939;
  pointer-events: none;
  overflow: hidden;
  z-index: 2;
}

.flip-card__back-bottom,
.flip-card__back-bottom-4digits {
  z-index: 1;
}

.flip-card__bottom::after,
.flip-card__back-bottom::after,
.flip-card__bottom-4digits::after,
.flip-card__back-bottom-4digits::after {
  display: block;
}
