@import '../sass/mixins/_mixins.sass';

.section-benefits {
  &__bg {
    // background-image: linear-gradient(to bottom, $surface-d-bg-d, #15191f);
    background: url('../assets/images/bg/bg-benefits@2x.jpg') no-repeat 55% 100%;
    background-size: 1920px;
    padding: 7.2rem 0 24.8rem;
    @include xl {
      background-position: 55% 100%;
      // background-size: 100%;
    }
    @include lg {
      padding: 3.2rem 0 3.2rem;
      background: url('../assets/images/bg/bg-benefits-mobile@2x.jpg') no-repeat 55% 12rem;
      background-size: 360px;
    }
    @include sm {
      background: url('../assets/images/bg/bg-benefits-mobile@2x.jpg') no-repeat 55% 12rem;
      background-size: 360px;
    }
  }
  position: relative;
  z-index: 1;
  @include lg {
    // background-image: linear-gradient(to bottom, #14191f, #191c22);
    background-image: linear-gradient(to bottom, #14191f, #191c22);
  }
  .mr-fix-1 {
    @include lg {
      margin-right: auto;
    }
  // bg-benefits.png;
  }
  .pre-title {
    text-align: center;
    @include lg {
      text-align: left;
    }
  }
  .title {
    text-align: center;
    @include lg {
      text-align: left;
    }
  }
  .desc {
    max-width: 53.6rem;
    margin: 0 auto;
    display: block;
    text-align: center;
    margin-bottom: 6.4rem;
    @include lg {
      max-width: 100%;
      text-align: left;
      margin-bottom: 3.2rem;
    }
  }
}

.benefit {
  @include lg {
    margin-bottom: 5.2rem;
  }
  &-last {
    @include lg {
      margin-bottom: 0;
    }
  }
  &__item {
    @include transition;
    &:hover {
      transform: translateY(-3px);
    }
    &-img {
      width: 4.8rem;
      height: 4.8rem;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 2.4rem;
      border-radius: 4px;
      &--1 {
        background-color: #66312d;
      }
      &--2 {
        background-color: #664d2f;
      }
      &--3 {
        background-color: #3a2066;
      }
      &--4 {
        background-color: #44523c;
      }

      svg {
        width: 4.8rem;
        height: 4.8rem;
      }
    }
    &-title {
      @include font(2rem, 1.4em, 600, -0.4px);
      color: $white;
      margin-bottom: 1.6rem;
      @include lg {
        @include font(1.6rem, 1.5em, 600);
        margin-bottom: .8rem;
      }
    }
    &-desc {
      @include font(1.4rem, 1.71em, 500);
      color: $text-d-60;
      @include lg {
        @include font(1.3rem, 1.54em, 500);
      }
    }
  }
}
