@import '../sass/mixins/_mixins.sass';

.section-roadmap {
  background-image: linear-gradient(to bottom, #191c22, #11151b);
  padding: 14.4rem 0 7.2rem;
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  @include lg {
    padding: 0 0 3.2rem;
  }
  .container {
    position: relative;
  }
  .row {
    position: relative;
    z-index: 110;
    & > div {
      position: relative;
      z-index: 220;
    }
  }
}

.roadmaps {
  &-title {
    max-width: 370px;
    .title {
      margin-bottom: 3.2rem;
      @include lg {
        margin-bottom: 2.4rem;
      }
    }
  }
}

.roadmap {
  display: flex;
  justify-content: flex-start;
  @include lg {
    position: relative;
    z-index: 50;
  }
  &-line {
    position: absolute;
    top: -1rem;
    right: calc(50% - 20.2rem);
    z-index: 1;
    @include lg {
      bottom: 21rem;
      left: 1.6rem;
      top: auto;
      right: auto;
      top: 0;
      overflow: hidden;
      display: flex;
      align-items: flex-end;
    }
  }
  &-box {
    margin-left: 2.4rem;
    // position: relative;
    // z-index: 50;
    @include lg {
      margin-left: 0;
      // overflow: hidden;
    }
  }
  &:not(:last-child) {
    margin-bottom: 5.6rem;
    @include media-b(1400) {
      margin-bottom: 4rem;
    }
    @include lg {
      margin-bottom: 3.2rem;
    }
  }
  &:hover {
    .roadmap-icon {
      svg {
        -webkit-transform: rotate(360deg);
        -webkit-transition-duration: 5s;
        -webkit-transition-delay: now;
        -webkit-animation-timing-function: linear;
        -webkit-animation-iteration-count: infinite;
      }
    }
  }

  &-icon {
    width: 4.8rem;
    height: 4.8rem;
    transform: translateY(-1rem);
    @include lg {
      transform: translateY(-.4rem);
    }

    svg {
      @include transition;
      @include lg {
        width: 3.2rem;
        height: 3.2rem;
      }
    }
  }
  &-info {
    padding-left: 2.4rem;
    @include lg {
      padding-left: 1.6rem;
    }
    div {
      color: $white;
      margin-bottom: 1.6rem;
      @include font(2rem, 1.4em, 600, -0.4px);
      @include lg {
        @include font(1.6rem, 1.5em, 600);
        margin-bottom: .8rem;
      }
    }
    span {
      color: $text-d-60;
      display: block;
      @include font(1.4rem, 1.71em, 500);
      @include lg {
        @include font(1.3rem, 1.54em, 500);
      }
    }
  }
  &__img {
    &--1 {
      margin-top: -15rem;
      margin-bottom: 2.4rem;
      @include xl {
        margin-top: 0;
      }
      @include lg {
        margin: 0 0 1.6rem;
      }
    }
    &--2 {
      @include lg {
        margin: 2.4rem 0 3.2rem;
      }
    }
  }
}
