@import '../sass/mixins/_mixins.sass';

.section-own {
  background-image: linear-gradient(to bottom, $surface-d-bg-d, #15191f);
  padding: 7.2rem 0;
  position: relative;
  z-index: 1;
  @include lg {
    padding: 2.4rem 0 3.2rem;
  }
  .title {
    @include lg {
      margin-bottom: 2.4rem;
    }
  }
  .desc {
    @include lg {
      margin: 0 0 3.2rem;
    }
  }
  .card {
    &:hover {
      z-index: 600;
    }
  }
}

.own {
  &__img {
    @include lg {
      position: relative;
      margin: 0 0 1.6rem;
    }
  }
}

.cards {
  display: flex;
  margin: 0 -1.5rem;
  position: relative;
  margin-top: -12rem;
  @include lg {
    margin: 0 -0.8rem;
    flex-wrap: wrap;
    align-items: flex-start;
    & > div {
      width: 50%;
    }
  }
  .card {
    @include lg {
      width: 100%;
      max-width: 100%;
    }
  }
  @include media-i(1200, 1399) {
    margin-top: -7rem;
  }
  @include xl {
    margin-top: -2rem;
  }
  @include lg {
    margin-top: 0;
  }
  &__pos {
    padding: 0 1.5rem;
    &:hover {
      position: relative;
      z-index: 10;
    }
    @include xl {
      padding: 0 .8rem;
    }
    @include lg {
      margin: 0 0 1.6rem;
    }
    &-button {
      margin-top: 4.8rem;
      display: flex;
      width: 100%;
      justify-content: center;
      @include lg {
        justify-content: flex-end;
        position: absolute;
        right: 0;
        bottom: 2rem;
      }
    }
    &--1,
    &--2 {
      margin-top: -3.2rem;
      @include lg {
        margin-top: 0;
      }
    }
    &--3 {
      margin-top: -6.4rem;
      @include lg {
        margin-top: 0;
      }
    }
    &--4 {
      margin-top: -20.6rem;
      @include lg {
        margin-top: 0;
      }
    }
    &--5 {
      margin-top: -6.4rem;
      @include lg {
        margin-top: 0;
      }
    }
  }
  .card {
    @include transition;
    &:hover {
      // transform: translateY(-.8rem);
      // margin-top: -10px;
    }
  }
}