@import '../sass/mixins/_mixins.sass';

.main-screen {
  padding: 16.8rem 0 8.2rem;
  box-sizing: border-box;
  min-height: 97.2rem;
  overflow: hidden;
  background: url('../assets/images/bg/bg-main@2x.jpg') no-repeat 50% 0;
  background-size: 1920px;
  @include lg {
    // display: none;
    padding: 11.2rem 0 3.2rem;
    background: #090d14 url('../assets/images/bg/bg-main-mobile@2x.jpg') no-repeat 50% 52rem;
    background-size: 360px;
  }
}

.main {
  &__roborovski {
    img {
      @include lg {
        width: 14rem;
        height: 14rem; }
      // opacity: 0;
      // @include transition;
      // &[lazy=loaded]
      //   opacity: 1
   }
  }

  &__logo {
    margin-bottom: 4.8rem;
    @include lg {
      margin-bottom: 3.2rem;
    }
    img {
      @include lg {
        width: 20rem;
        height: 4rem;
      }
    }
  }

  &__countdown {
    height: 104px;
    margin-bottom: 2.4rem;
    position: relative;
    width: 42.4rem;
    @include lg {
      width: 100%;
      max-width: 100%;
      margin-bottom: 1.6rem;
    }
    &-item {
      padding: 1.6rem;
      border-radius: 12px;
      -webkit-backdrop-filter: blur(40px);
      backdrop-filter: blur(40px);
      box-shadow: 8px 8px 24px 0 rgba(9, 13, 20, 0.4), -4px -4px 8px 0 $text-d-text-d-4, 0 1px 1px 0 rgba(9, 13, 20, 0.4);
      border: solid 1px $bg-l-6;
      background-image: linear-gradient(104deg, $bg-l-12, $bg-l-6 52%, rgba(245, 247, 250, 0) 100%);
      width: 100%;
      @include lg {
        width: auto;
        display: inline-flex;
        justify-content: flex-start;
      }

      &-saleisopen {
        color: $white;
      }
    }
  }

  .desc {
    margin-bottom: 3.2rem;
    @include font(2rem, 1.8em, 500);
    color: $white;
    @include lg {
      @include font(1.4rem, 1.71em, 500);
      margin-bottom: 2.4rem;
    }
  }

  &-lead {
    margin-bottom: 4.8rem;
  }
}

.main {
  &__lead {
    display: flex;
    justify-content: flex-end;
    @include lg {
      margin-top: 29.6rem;
      justify-content: space-between;
    }
    .card-item__info-row {
      @include lg {
        flex-direction: row;
        justify-content: space-between;
      }
    }
    .card-item__info-col1 {
      @include lg {
        flex-grow: 1;
      }
    }
    .card-item__info-col2 {
      @include lg {
        flex-direction: column;
        margin-top: 0;
        justify-content: center;
        align-items: center;
        width: auto;
        .card-item__icon {
          margin: 0;
          &:not(:last-child) {
            margin: 0 0 .4rem;
          }
        }
      }
    }
    &-actions {
      @include lg {
        order: 2;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      .video__box-btn {
        @include lg {
          top: auto;
          left: auto;
          position: relative;
        }
      }
    }
    .howtobuy {
      margin-right: 1.6rem;
      width: 9.6rem;
      min-width: 9.6rem;
      height: 9.6rem;
      padding: 1px;
      display: flex;
      background-image: linear-gradient(135deg, $secondary, $primary 100%);
      border-radius: 50%;
      padding: 2px;
      cursor: pointer;
      @include lg {
        width: 7.2rem;
        height: 7.2rem;
        max-width: 7.2rem;
        min-width: 7.2rem;
        margin: 0 0 0;
      }
      a {
        display: flex;
        background-image: linear-gradient(to bottom, $card-d-2, $card-d-2), linear-gradient(135deg, $secondary, $primary 100%);
        border-radius: 50%;
        text-align: center;
        @include font(1.4rem, 1.43em, 600);
        color: $white;
        align-items: center;
        @include lg {
          @include font(1.3rem, 1.23em, 600);
        }
        i {
          font-style: normal;
        }
      }
    }

    .card {
      min-width: 20rem;
      @include lg {
        order: 1;
      }
    }
  }

  &__mint {
    font-family: Karantina;
    color: $white;
    @include font(3.6rem, 1em, bold);
    text-transform: uppercase;
    background-image: linear-gradient(135deg, $secondary, $primary 100%);
    right: 1.6rem;
    top: 1.6rem;
    z-index: 1;
    padding: 2px;
    display: flex;
    flex-direction: column;
    position: absolute;
    border-radius: 4px 4px 12px 12px;
    cursor: pointer;
    @include lg {
      top: calc(10.4rem + 1.6rem);
      right: 0;
    }
    &-i {
      display: flex;
      flex-direction: column;
      padding: 1.6rem 1.6rem 1.6rem 2rem;
      background-color: $card-d-2;
      border-radius: 4px 4px 12px 12px;
    }
    img {
      width: 3.6rem;
      height: 3.6rem;
    }
    &-row {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
  }
  &__video {
    margin-top: 5.6rem;
    display: flex;
    width: 100%;
    justify-content: flex-end;
  }
}

.video {
  &__box {
    width: 31.2rem;
    max-width: 31.2rem;
    min-width: 31.2rem;
    height: 31.2rem;
    border-radius: 50%;
    position: relative;
    &-color {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      border-radius: 50%;
      // background-color: rgba(255,255,255,.1);
      // background-color: rgba(#2d2f33, .4);
      z-index: 1;
      -webkit-backdrop-filter: blur(40px);
      backdrop-filter: blur(40px);
      background-image: linear-gradient(143deg, rgba(245, 247, 250, 0.24), rgba(245, 247, 250, 0.12) 52%, rgba(245, 247, 250, 0.12) 100%);
    }
    &-btn {
      position: absolute;
      left: calc(50% - 3.6rem);
      top: calc(50% - 3.6rem);
      z-index: 50;
      .btn-round {
        width: 7.2rem;
        height: 7.2rem;
      }
    }
    &-item {
      position: relative;
      position: absolute;
      // width: 1px;
      // height: 1px;
      // top: 50%;
      // left: 50%;
      width: 31.2rem;
      height: 31.2rem;
    }
    &-round {
      width: 31.2rem;
      height: 31.2rem;
      // top: calc(50% - 15.6rem);
      // left: calc(50% - 15.6rem);
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
    }
    &-text {
      width: 31.2rem;
      height: 31.2rem;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.fcards {
  min-width: 20rem;
  position: relative;
  height: 26rem;
  .card {
    position: absolute;
    top: 0;
    right: 0;
  }
  .fcard-1 {
    z-index: 100;
  }
  .fcard-2 {
    transform: scale(.95) translateY(1.5rem);
    z-index: 10;
  }
  .fcard-3 {
    z-index: 1;
    transform: scale(.90) translateY(3.2rem);
  }
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.rotateAnimate {
  -webkit-animation: rotating 12s linear infinite;
  -moz-animation: rotating 12s linear infinite;
  -ms-animation: rotating 12s linear infinite;
  -o-animation: rotating 12s linear infinite;
  animation: rotating 12s linear infinite;
}
