@import '../sass/mixins/_mixins.sass';

.team {
  position: relative;
  margin-bottom: 3.2rem;
  max-width: 20rem;
  @include lg {
    max-width: 14rem;
    margin-bottom: 2.4rem;
  }

  &-card {
    cursor: pointer;
    background-color: transparent;
    width: 20rem;
    height: 20rem;
    perspective: 1000px;
    margin-bottom: .8rem;
    // @include transition;
    @include lg {
      width: 14rem;
      height: 14rem;
    }
    img {
      @include transition;
    }
    &-inner {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: center;
      transition: transform 0.8s;
      transform-style: preserve-3d;
    }
    &:hover {
      // transform: scale(1.05);
      .team-card-inner {
        transform: rotateY(180deg);
      }
      //.team-card__state,
      //.team-card__hover {}
    }
    &__state,
    &__hover {
      position: absolute;
      width: 100%;
      height: 100%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      img {
        max-width: 100%;
      }
    }
    //&__state
    &__hover {
      transform: rotateY(180deg);
    }
  }

  &-bottom {
    position: relative;
  }

  &-name {
    @include font(2rem, 1.4em, 600, -0.4px);
    color: $white;
    @include lg {
      @include font(1.6rem, 1.5em, 600);
    }
  }

  &-info {
    position: absolute;
    top: .2rem;
    right: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @include lg {
      flex-direction: column;
      justify-content: flex-start;
    }
    &__horizontal {
      @include lg {
        flex-direction: row;
        justify-content: flex-end;
      }
    }
    & > a {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      @include lg {
        margin: 0 0 .4rem;
      }
      svg {
        @include lg {
          width: 2rem;
          height: 2rem;
        }
        path {
          @include transition;
        }
      }
      &:hover {
        svg {
          path {
            fill-opacity: 1;
          }
        }
      }
      &:not(:first-child) {
        margin-left: .8rem;
        @include lg {
          margin: 0 0 .4rem;
        }
      }
    }
  }
}

.partners {
  @include lg {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    img {
      max-width: 14rem;
    }
  }
}

