@import '../sass/mixins/_mixins.sass';

.card {
  position: relative;
  border: solid 1px $bg-l-6;
  padding: .4rem;
  border-radius: 12px;
  max-width: 20rem;
  // &:hover {
  //   z-index: 500;}
  &-overlay {
    border-radius: 12px;
    -webkit-backdrop-filter: blur(40px);
    backdrop-filter: blur(40px);
    box-shadow: 8px 8px 24px 0 rgba(9, 13, 20, 0.4), -4px -4px 8px 0 $text-d-text-d-4, 0 1px 1px 0 rgba(9, 13, 20, 0.4);
    background-image: linear-gradient(143deg, $bg-l-12, $bg-l-6 52%, rgba(245, 247, 250, 0) 100%);
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  &-item {
    position: relative;
    z-index: 2;
    &:hover {
      // z-index: 500;
    }
    &__icon {
      display: flex;
      width: 2rem;
      min-width: 2rem;
      height: 2rem;
      border-radius: .4rem;
      align-items: center;
      justify-content: center;
      &--1 {
        background-image: linear-gradient(to bottom, #d78ffb, #a0fef9);
      }
      &--2 {
        background-image: linear-gradient(to bottom, #ceffad, #618c8f);
      }
      &--3 {
        background-image: linear-gradient(to bottom, #d599e1, #f5a7b4 53%, #ffcbad);
      }
      &--4 {
        background-image: linear-gradient(to bottom, #fbe2c4, #fea7a0);
      }
      &--5 {
        background-image: linear-gradient(to bottom, #ade9ff, #ac99e1);
      }
      &--6 {
        background-image: linear-gradient(to bottom, #faffad 1%, #ffcbad);
      }
    }
    &__image {
      // position: relative;
      // z-index: 100;
      // margin-bottom: .4rem;
      img {
        @include transition;
        border-radius: .4rem;
        position: relative;
        &:hover {
          transform: scale(1.8);
          z-index: 600;
        }
      }
    }
    &__info {
      padding: .8rem .8rem .8rem 1.2rem;
      background-color: rgba(40, 42, 46, 0.8);
      border-radius: .8rem;
      margin-top: .4rem;
      &-name,
      &-summ,
      &-row {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        @include lg {
          flex-direction: column;
          justify-content: flex-start;
        }
      }
      &-col1 {
        @include lg {
          width: 100%;
        }
        span {
          @include font(1.4rem, 1.71em, 600);
          display: block;
          color: $white;
        }
        div {
          @include font(1.3rem, 1.85em, 700, 1px);
          display: block;
          color: $white;
          // font-family: 'Druk Wide Cy Web Super';
        }
      }
      &-col2 {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        @include lg {
          flex-direction: row;
          justify-content: flex-start;
          align-items: flex-start;
          margin: .8rem 0 0;
          width: 100%;
        }
        .card-item__icon {
          &:not(:last-child) {
            margin: 0 0 .4rem;
            @include lg {
              margin: 0 .8rem 0 0;
            }
          }
        }
      }
    }
  }
}
