@import '../sass/mixins/_mixins.sass';

.section-team {
  padding: 7.2rem 0;
  background-image: linear-gradient(to bottom, #11151b, $socket-d);
  overflow: hidden;
  @include lg {
    padding: 3.2rem 0;
  }
  .desc {
    padding: 4.8rem;
    margin: 0;
    @include media-i(992, 1400) {
      max-width: 19rem;
      padding: .8rem;
    }
    @include lg {
      padding: 0;
      margin: 0 0 3.2rem;
    }
  }
  .container {
    position: relative;
  }
  .row {
    position: relative;
    z-index: 10;
  }
  .team {
    @include media-i(992, 1400) {
      max-width: 19rem;
    }
  }
}

.join-btn {
  margin-top: 14.4rem;
  border-radius: 1.2rem;
  padding: 1.2rem;
  display: block;
  cursor: pointer;
  display: flex;
  justify-content: center;
  background-color: #66312d;

  font-family: Karantina;
  @include font(4.8rem, 1em, bold);
  color: #fe7a70;
  text-transform: uppercase;
  @include lg {
    margin-top: 3.2rem;
    @include font(3rem, 1em, bold);
    justify-content: center;
    text-align: center;
  }
}

.partner__img {
  margin-bottom: 1.6rem;
  img {}
}

